.stars {
  display: flex;

  &__star {
    background: url(../images/star.svg) no-repeat 50%;
    height: 16px;
    width: 16px;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  &--1 &__star:nth-child(1),
  &--2 &__star:nth-child(-n + 2),
  &--3 &__star:nth-child(-n + 3),
  &--4 &__star:nth-child(-n + 4),
  &--5 &__star:nth-child(-n + 5) {
    background-image: url(../images/star-active.svg);
  }
}
